.anim {
  -webkit-animation: anim 2s ease-out 0s 1;
          animation: anim 2s ease-out 0s 1;
}

@-webkit-keyframes anim {
  0% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
  }
  100% {
    transfrom: translateY(0%);
  }
}

@keyframes anim {
  0% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
  }
  100% {
    transfrom: translateY(0%);
  }
}

#portfolio-flex-container {
  display: -ms-flexbox;
  display: flex;
