.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  -webkit-transition: 0.5s ease;
  -o-transition: 0.5s ease;
  transition: 0.5s ease;
  background-color: black;
  white-space: pre-wrap;
  font-family: "Roboto", sans-serif;
  color: #18cae6;
}
section:hover .overlay {
  cursor: pointer;
  opacity: 1;
  z-index: 8;
  text-align: center;
  color: #18cae6;
}

